import { ApplyAddFriendRequest } from '@/network/addfriend.js'
import { mapState } from 'vuex'

export default {
	data() {
		return {
			friendPhone: null,
			requestMsg: '', //请求信息
		}
	},
	computed: {
		...mapState(['userInfo'])
	},
	methods: {
		finishedApply() {
			if(this.requestMsg === '') {
				uni.showToast({
					title: '请求信息不能为空',
					icon: 'none'
				})
				return
			}
			let obj = {
				sender: this.userInfo.user.userPhone,
				receiver: this.friendPhone,
				addType: 1,
				requestMsg: this.requestMsg
			}
			ApplyAddFriendRequest(obj).then(res => {
				if(res.status === 200) {
					console.log(res)
					if(res.data.code === 4003) {
						uni.showToast({
							title: '请求已发送',
							mask: true,
							success: () => {
								setTimeout(() => {
									uni.navigateBack()
								}, 1000)
							}
						})
					}else if(res.data.code === 2000) {
						uni.showToast({
							title: '添加好友请求发送成功',
							mask: true,
							success: () => {
								setTimeout(() => {
									uni.navigateBack()
								}, 1000)
							}
						})
						
					} else {
						uni.showToast({
							title: '请求发送失败，请稍后重试',
							icon: 'none'
						})
					}
					
				}else {
					uni.showToast({
						title: '网络连接错误',
						icon: 'none'
					})
				}
			}).catch(err => {
				console.log(err)
				uni.showToast({
					title: '添加失败,请稍后重试',
					icon: 'none'
				})
			})
		}
	},
	onLoad(option) {
		console.log(option);
		this.friendPhone = option.friendPhone;
	},
	onNavigationBarButtonTap(option) {
		if(option.index === 0) {
			//点击完成申请好友
			this.finishedApply()
		}
	}
}
